.cube-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80vh; /* Full screen height */
    background-color: #f4f4f4; /* Light background */
    perspective: 800px; /* Adds depth to the 3D effect */
  }

  .cube {
    position: absolute; /* Ensures the cube stays within the container */
    width: 100px; /* Define a fixed width */
    height: 100px; /* Define a fixed height */
    transform-style: preserve-3d; /* Ensures cubies are rendered in 3D */
    transform-origin: center center; /* Rotate around the center of the cube */
    transition: transform 0.3s ease-out; /* Smooth transition for rotation */
  }
  

.cubie {
    position: absolute;
    width: 44px;
    height: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
    transform-style: preserve-3d;
    transition: transform 0.3s ease-in-out; /* âœ… Ensures smooth rotation */
}


/* Base arrow styles */
.rotation-arrow {
  position: absolute;
  top: 50%; left: 50%;
  transform: translate(-50%, -50%);
  font-size: 2rem;
  color: red;
  z-index: 9999;
  animation: pulse 1s infinite alternate;
}

/* Face-specific tweaks, if needed */
.rotation-arrow-u {
  /* e.g. flip or shift if it's upside-down on the Up face */
  transform: translate(-50%, -50%) rotate(180deg);
}

.rotation-arrow-d {
  /* ... */
}

.rotation-arrow-f {
  /* ... */
}

/* etc. */

  
  /* Position arrows over each face */
  .face-U .rotation-arrow { top: 20px; left: 50%; transform: translateX(-50%); }
  .face-D .rotation-arrow { bottom: 20px; left: 50%; transform: translateX(-50%); }
  .face-F .rotation-arrow { top: 50%; left: 50%; transform: translate(-50%, -50%); }
  .face-B .rotation-arrow { top: 50%; right: 20px; transform: translateY(-50%); }
  .face-L .rotation-arrow { top: 50%; left: 20px; transform: translateY(-50%); }
  .face-R .rotation-arrow { top: 50%; right: 20px; transform: translateY(-50%); }
  
  .success-message {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: green;
    color: white;
    padding: 20px;
    border-radius: 10px;
    font-size: 1.5rem;
  }
  

  